import './css/index.less';
import {widget_seatmap_strategies} from './widgetSeatmapStrategies';

function entry() {
    window.onecheckin_widget_seatmap_strategies = widget_seatmap_strategies;
    document.addEventListener('DOMContentLoaded', configure, false);
}

function configure() {
    if (__CONFIG__.sentry.isEnabled) {
        Raven.config(__CONFIG__.sentry.publicDSN, {
            release: __CONFIG__.sentry.release
        }).install();
    }
}

entry();
