import Mustache from 'mustache';
import {
    seatmap_strategies, seatmap_preview
} from '../../v1.0/src/viewSeatmapStrategies'
import {addEvent, getScrollTop, setScrollTop} from '../../v1.0/src/helpers';

export class widget_seatmap_strategies extends seatmap_strategies {
    constructor(container, options) {
        const psg_count = options.psg_count || 1;
        super(
            `seatmap_strategies-${psg_count}/`,
            new app({
                endpoints: __CONFIG__.endpoint,
                cssPrefix:  __CONFIG__.cssPrefix
            }),
            'widget_seatmap_strategies',
            {num: psg_count}
        );
        this.template_url = options.template_url || __CONFIG__.template_url;
        this.template = options.template || 'seatmap_strategies';
        this.onchange = options.onchange;

        if (typeof(container) === 'string') {
            this.view = this.app.root = window.document.getElementById(container);
        }
        else {
            this.view = this.app.root = container;
        }
        this.data.num = psg_count;
        if (window.document.getElementById(this.template)) {
            this.init();
        }
        else {
            this.app.ajax({
                url: this.template_url,
                callback: text => {
                    const div = document.createElement('div');
                    div.style.display = 'none';
                    div.innerHTML += text;
                    document.body.appendChild(div);
                    this.init();
                },
                error: (status, text) => {
                    console.error(`get_template error ${status}: ${text}`);
                }
            });
        }
    }

    init() {
        this.data.strategies = JSON.parse(
            window.document.getElementById('seatmap_strategies_dict').innerHTML
        )[this.data.num];
        this.get();
        this.render();
        this.strategiesChange();
    }
    strategiesChange(event, applyStrategyXs) {
        super.strategiesChange(event, applyStrategyXs);
        if (this.onchange) this.onchange(this.data.data);
    }
}

class app {
    constructor(params) {
        Mustache.tags = ['<%', '%>'];
        this.Mustache = Mustache;
        this.settings = params;
        this.cache = {};
        this.default_headers = {
            'Content-Type': 'application/json;charset=UTF-8'
        };
        this.endpoints = params.endpoints;
    }

    renderIcons(param_root) {
        const root = param_root || this.root;
        const cssPrefix = this.settings.cssPrefix.prefix;
        root.querySelectorAll(`.${cssPrefix}icon`).forEach((icon) => {
            const m = icon.className.match(/(icon-\S+)/);
            if (m && document.getElementById(m[1])) {
                icon.innerHTML = document.getElementById(m[1]).innerHTML;
            }
        });
    }

    bindLinks(param_root) {
        const root = param_root || this.root;
        addEvent(
            root.querySelectorAll('[data-toggle="collapse"]'),
            'click',
            function onClick(event) {
                event.stopImmediatePropagation();
                event.stopPropagation();
                event.preventDefault();
                const href = this.dataset.target || this.getAttribute('href');
                this.closest(href).classList.toggle('in');
            }
        );
    }

    ajax(options) {
        const headers = Object.assign(this.default_headers, options.headers)
        const xhr = new XMLHttpRequest();
        try {
            xhr.timeout = options.timeout || this.settings.xhr_timeout;
        } catch (err) {}
        if (typeof (options.error) !== 'function') {
            options.error = (status, data) =>  {
                this.error(status, data);
            }
        };
        if (typeof (options.callback) !== 'function') {
            options.callback = () =>  {}
        };
        xhr.open(options.method ? options.method : 'GET', options.url, true);
        xhr.onreadystatechange = function onreadystatechange() {
            if (xhr.readyState === 4) {
                try {
                    xhr.responseJSON = JSON.parse(xhr.responseText);
                } catch (err) {};
                if (xhr.status >= 200 && xhr.status < 299) {
                    options.callback(xhr.responseJSON || xhr.responseText);
                }
                else {
                    options.error(xhr.status,
                        xhr.responseJSON || xhr.responseText
                    );
                }
            }
        };
        xhr.ontimeout = () => {
            options.error(0);
        };
        Object.keys(headers).forEach((header) => {
            const val = headers[header];
            if (val !== null) {
                xhr.setRequestHeader(header, val);
            }
        });
        const data = options.data ? JSON.stringify(options.data) : null;
        try {
            xhr.send(data);
        } catch (err) {
            this.error(0, err);
        }
    }
    error(code, desc) {
        window.console.error(code, desc);
    }
    initObject(uri, className, data, update_cache) {
        if (uri in this.cache && !update_cache) return this.cache[uri];
        let ViewClass;
        if (className === 'seatmap_preview') {
            ViewClass = seatmap_preview;
        }
        if (!ViewClass) return;
        const object = new ViewClass(uri, this, className, data || {});
        this.cache[object.uri] = object;
        return object;
    }

    get(uri, className, data, callback, force) {
        let obj = this.cache[uri];
        const after = () => {
            obj.show();
            if (typeof(callback) === 'function') {
                callback();
            }
            return obj;
        }
        if (!obj) {
            obj = this.initObject(uri, className, data);
            return obj.get(force, after);
        }
        return after();
    }

    scrollTo(obj) {
        const hop = 1/50;
        const time = 350;
        const func = (t) => {return t*(2-t)};
        let dist =0;
        let scrollTop = getScrollTop();
        if (obj) {
            const rect = obj.getBoundingClientRect();
            dist = rect.top;
        };
        if (dist === 0) return;

        const animate = (progress) => {
            setScrollTop(scrollTop + dist*func(progress));
            if (progress < 1) {
                setTimeout(() => {animate(progress + hop)}, time * hop);
            }
        };
        animate(hop);
    }

}

