import {toggleClass, addEvent, is_xs, deepcopy} from './helpers';
import {GenericComponent} from './GenericComponent';
import {SeatMap} from './compSeatMap'
import {dragList} from './compDragList'

export class seatmap_strategies extends GenericComponent {
    constructor(uri, app, className, data) {
        super(uri, app, (className || 'seatmap_strategies'), data);
        this.default_modifiers = {front: true, middle: true, rear: true};
        this.css_prefix = this.app.settings.cssPrefix
            && this.app.settings.cssPrefix.prefix || '';
    }

    get(forceUpdate, callback, fail) {
        if (!this.data.data) this.data.data = [];
        const strategiesList = deepcopy(this.data.data);
        const strategiesDir = {};
        this.data.strategies.forEach(strategy => {
            if (!strategy) return;
            strategiesDir[strategy.id] = strategy;
            if (!strategiesList.find(el => {
                return typeof(el) === 'string'
                    ? strategy.id  === el
                    : el.name == strategy.id
            })) {
                strategiesList.push(strategy.id);
            }
        })
        const count = Math.min(
            Math.max(4, this.data.data.length),
            strategiesList.indexOf('find_any_seat')+1,
            strategiesList.length
        );
        this.data.strategiesList = strategiesList.map((strategy, i) => {
            let str_name, modifiers;
            if (typeof(strategy) === 'string') {
                str_name = strategy;
                modifiers = this.default_modifiers;
            }
            else {
                str_name = strategy.name;
                modifiers = strategy.modifiers || this.default_modifiers;
            }
            if (!strategiesDir[str_name]) {
                console.error(`unknown strategy ${str_name}`);
                return {strategy: str_name, index: i+1};
            }
            const tmp = deepcopy(strategiesDir[str_name]);
            if (tmp.has_modifiers) tmp.modifiers = modifiers;
            tmp.index = i+1;
            tmp.strategy = tmp.id;
            return tmp;
        }).splice(0, count);

        if (typeof (callback) === 'function') {
            callback();
        }
        return this;
    }

    applyStrategy(seatmap, strategies, persons) {
        const that = this;
        const strData = {};
        const error = seatmap.view.querySelector('[data-bind="seats-not-found"]');
        strData[persons] = strategies;
        that.app.ajax({
            method: 'POST',
            data: {
                data: {
                    seatmap: seatmap.seatmap.seatmap,
                    strategies: strData,
                    persons
                }
            },
            url: `${that.app.settings.endpoints}seatmap/apply_strategy/`,
            callback(data) {
                seatmap.clearCurrentSeats();
                if (!data || !data.data || !data.data.length) {
                   error.classList.add('in');
                   return;
                }
                seatmap.setCurrentSeats(data.data);
                error.classList.remove('in');
                if (!is_xs()) {
                    seatmap.seatmap.renderSeatmap();
                }
            }
        });
    }

    seatmap_preview() {
        return this.app.cache[
            this.view.querySelector('.obj-seatmap_preview').dataset.uri
        ];
    }

    static serializeModifiers(plane)  {
        let all_true = true;
        let all_false = true;
        const modifiers = {};
        plane.querySelectorAll('input').forEach(plane_selector => {
            modifiers[plane_selector.name] = plane_selector.checked;
            all_true = all_true && plane_selector.checked;
            all_false = all_false && !plane_selector.checked;
        })
        return (all_true || all_false) ? null : modifiers;
    }

    open() {
        if (this.opened) return;
        this.opened = true;
        this.strategiesChange();
    }

    strategiesChange(event, applyStrategyXs) {
        const strategiesWithState = []
        const strategies = Array.prototype.map.call(
            this.view.querySelectorAll('[name="strategy"]'),
            ctl => ctl.value
        );
        this.view.querySelectorAll('[data-bind="plane-selector"]').forEach(
            (plane, i) => {
                const has_modifiers = this.data.strategies.find(
                    dic => dic.id === strategies[i]
                ).has_modifiers;
                let modifiers = has_modifiers
                    ? seatmap_strategies.serializeModifiers(plane)
                    : null;
                if (modifiers) {
                    strategies[i] = {name: strategies[i], modifiers}
                }
                else if (has_modifiers) {
                    modifiers = this.default_modifiers;
                };
                toggleClass(plane, 'hidden', !has_modifiers);
                strategiesWithState.push({
                    index: i+1,
                    strategy: strategies[i],
                    modifiers,
                    has_modifiers
                });
            }
        );
        this.data.strategiesList = strategiesWithState;
        this.formSubmitToggle(false);
        this.data.data = strategies;
        if (!is_xs() || applyStrategyXs) {
            this.applyStrategy(this.seatmap_preview(), strategies, this.data.num);
        };
    }

    initForm() {
        const seatmap_container = this.view.querySelector('[data-bind="seatmap-container"]');
        console.log(`Creating draglist with prefix '${this.css_prefix}'`);
        const dlist = new dragList(this.view.querySelector('[data-bind="drag-list"]'), {
            drag_holder_class: `${this.css_prefix}drag-holder`,
            drag_class: `${this.css_prefix}drag`,
            dragging_class: `${this.css_prefix}dragging`,
        });

        const that = this;
        super.initForm();

        dlist.on('change', event => this.strategiesChange(event));
        addEvent(this.view.querySelectorAll('[data-bind="apply"]'), 'click',
            () => {
                this.strategiesChange(null, true);
                if (is_xs()) {
                    seatmap_container.classList.add('in');
                    this.app.scrollTo(seatmap_container);
                }
            }
        );
        addEvent(this.view.querySelectorAll('[data-bind="add-strategy"]'), 'click',
            event => {
                const has_modifiers = this.data.strategies[0].has_modifiers;
                this.data.strategiesList.push({
                    index: this.data.strategiesList.length,
                    strategy: this.data.strategies[0].id,
                    modifiers: has_modifiers ? this.default_modifiers : null,
                    has_modifiers
                });
                this.render();
            }
        );
        addEvent(this.view.querySelectorAll('[name="strategy"]'), 'change',
            event => this.strategiesChange(event)
        );
        addEvent(
            this.view.querySelectorAll('[data-bind="plane-selector"] button'),
            'click',
            function planeClick(event) {
                const plane = this.closest('[data-bind="plane-selector"]');
                if (
                    !this.classList.contains('active')
                    && !seatmap_strategies.serializeModifiers(plane)
                ) {
                    console.log('can\'t unchecked last plane section');
                    this.classList.add('active');
                    this.querySelector('input').checked = true;
                    return;
                }
                that.strategiesChange(event);
            }
        );
        addEvent(this.view.querySelectorAll('[data-bind="show"]'), 'click',
            event => {
                event.preventDefault();
                const parent = event.target.closest('[data-bind="drag"]');
                const strategy = this.data.strategies.find(
                    dic => dic.id === parent.querySelector('[name="strategy"]').value
                );
                const seatmap_preview = this.seatmap_preview();
                const modifiers = seatmap_strategies.serializeModifiers(
                    parent.querySelector('[data-bind="plane-selector"]')
                );
                if (strategy.has_modifiers && modifiers) {
                    this.applyStrategy(seatmap_preview, [{
                        name: strategy.id, modifiers
                    }], this.data.num);
                }
                else {
                    this.applyStrategy(seatmap_preview,[strategy.id], this.data.num);
                }
                if (is_xs()) {
                    seatmap_container.classList.add('in');
                    this.app.scrollTo(seatmap_container);
                }
            }
        );
        addEvent(this.view.querySelectorAll('[data-bind="back"]'), 'click',
            event => {
                event.preventDefault();
                seatmap_container.classList.remove('in');
                this.app.scrollTo(this.view);
            }
        );
    }

    formSubmit(form, event) {
        this.parent.formSubmit(form, event);
    }
}

export class seatmap_preview extends GenericComponent {
    constructor(uri, app, className, data) {
        super(uri, app, (className || 'seatmap_preview'), data);
        this.events = {};
    }

    init() {
        if (this.initialized) return;
        const seatmap_preview_presets = document.getElementById('seatmap_preview_presets');
        const seatmap = document.getElementById('seatmap');
        if (seatmap_preview_presets && seatmap) {
            this.presets = JSON.parse(seatmap_preview_presets.innerHTML);
            this.seatmap = new SeatMap({
                seatmap: JSON.parse(JSON.stringify(this.presets[0])),
                Mustache: this.app.Mustache,
                is_selectable: false,
                is_example: true,
                templates: {
                    seatmap: seatmap.innerHTML,
                }
            });
            this.initialized = true;
        }
    }

    render() {
        const that = this;
        this.init();
        this.data.presets = new Array();
        this.presets.forEach((preset, index) => {
            that.data.presets.push({
                name: preset.aircraft,
                active: index == that.data.preset
            });
        });
        GenericComponent.prototype.render.call(this);
        this.seatmap.container = this.view.querySelector('[data-bind="seatmap-container"]');
        this.seatmap.renderSeatmap();
    }

    on(event_type, callback) {
        this.events[event_type] = callback;
    }

    setCurrentSeats(seats) {
        seats.forEach((seat) => {
            this.seatmap.getSeat(seat).isCurrent = true;
        });
        this.seatmap.renderSeatmap();
    }

    clearCurrentSeats() {
        this.seatmap.getCurrentSeats().forEach((seat) => {
            this.seatmap.setSeat(seat, {isEmpty: true});
        });
    }
}


