import {addEvent} from './helpers';

export class SeatMap {
    constructor(params) {
        this.params = params;
        this.container = params.container;
        this.seatmap = this.params.seatmap;
        this.events = {};
        this.Mustache = params.Mustache || Mustache;
    }

    renderSeatmap(params) {
        const root = this.container;
        let html,
            i,
            j;
        const seatmap = this.seatmap;
        let maxRow = 0;
        let startNum = seatmap.seatmap[0] ? seatmap.seatmap[0].startNum : 100;
        const view = {
            is_selectable: this.params.is_selectable,
            is_example: this.params.is_example,
            columns: [],
            rows: [],
        };

        if (!this.seatmap ||
            !this.seatmap.seatmap ||
            (!('length' in this.seatmap.seatmap))
        ) {
            root.innerHTML = this.Mustache.render(this.params.templates.seatmap, {});
            return;
        }

        for (i = 0; i < seatmap.seatmap.length; i += 1) {
            view.columns.push({
                name: seatmap.seatmap[i].rows ? seatmap.seatmap[i].seatName : '',
                i
            });
            if (seatmap.seatmap[i].seatsCount) {
                maxRow = Math.max(maxRow, seatmap.seatmap[i].seatsCount);
                startNum = Math.min(startNum, seatmap.seatmap[i].startNum);
            }
        }

        for (i = 0; i < maxRow; i++) {
            const row = {
                i,
                num: i + startNum,
                cols: new Array()
            };
            for (j = 0; j < seatmap.seatmap.length; j++) {
                if (!seatmap.seatmap[j].rows) {
                    row.cols.push({
                        space: true
                    });
                } else {
                    let seat = {};
                    if (seatmap.seatmap[j].rows[i]) {
                        seat = Object.assign(seat, seatmap.seatmap[j].rows[i]);
                        if (typeof (seat.isExists) === 'undefined') {
                            seat.isExists = true;
                        }

                        seat.classList = new Array();
                        for (const key in seat) {
                            if (key.match(/^is/) && seat[key]) {
                                seat.classList.push(key);
                            }
                        }
                        seat.index = j;
                        if (seat.isExists) {
                            seat.name = (seatmap.seatmap[j].startNum + i) + seatmap.seatmap[j].seatName;
                        }
                    }
                    row.cols.push(seat);
                }
            }
            view.rows.push(row);
        }

        const that = this;
        root.innerHTML = this.Mustache.render(this.params.templates.seatmap, view);
        addEvent(root.querySelectorAll('tbody td'), 'click', function (event) {
            that.seatmapClick(this, event);
        });

        if (this.params.is_example) {
            const container = root.querySelector('[data-bind="seatmap"]');
            const svg_text = window.document.getElementById('text-example')
                .innerHTML.replace(/>\s+</g, '><').trim();
            container.style.backgroundImage =
                `url('data:image/svg+xml;utf8,${svg_text}')`;
        }
    }

    on(event, callback) {
        this.events[event] = callback;
    }

    off(event) {
        this.events[event] = null;
    }

    getCurrentSeats() {
        const seats = [];
        this.seatmap.seatmap.forEach(col => {
            if (col.rows && col.rows.length) {
                col.rows.forEach((seat, index) => {
                    if (seat.isCurrent) { seats.push((col.startNum + index) + col.seatName); }
                });
            }
        });
        return seats;
    }

    getSeat(name, state) {
        const m = (`${name}`).match(/(\d+)(\w+)|(\w+)(\d+)/);
        let col,
            row,
            seatName;
        if (!m) return;
        if (parseInt(m[1])) {
            seatName = m[2];
            row = parseInt(m[1]);
        } else {
            seatName = m[3];
            row = parseInt(m[4]);
        }
        if (row == NaN || !seatName) return;
        col = this.seatmap.seatmap.find(
            col => col.seatName === seatName.toUpperCase()
        );
        return col && col.rows ?
            col.rows[row - col.startNum] : null;
    }

    setSeat(name, state) {
        let key,
            seat = this.getSeat(name);
        if (!seat) return;
        for (key in seat) { delete seat[key]; }
        for (key in state) { seat[key] = state[key]; }
        return seat;
    }

    has_paid_seats(seatmap) {
        if (typeof (seatmap) === 'undefined') { seatmap = this.seatmap; }
        seatmap = seatmap.seatmap;
        let has_paid_seats = false;
        if (!seatmap || !seatmap.length) return false;
        seatmap.forEach((col) => {
            if (col.rows && col.rows.length) {
                col.rows.forEach((cell) => {
                    if (cell.isPaid) has_paid_seats = true;
                });
            }
        });

        return has_paid_seats;
    }

    seatmapClick(target, event) {
        event.stopPropagation();
        event.preventDefault();

        const col = parseInt(target.dataset.index);
        const row = parseInt(target.closest('tr').dataset.index);
        let column = null;
        if (col !== NaN && row !== NaN && this.seatmap && this.seatmap.seatmap) { column = this.seatmap.seatmap[col]; }

        if (column) {
            const name = `${row + column.startNum}${column.seatName}`;

            if (typeof (this.events.click) === 'function') {
                this.events.click.call(this, {
                    seat: this.getSeat(name),
                    name
                });
            }
        } else {
            console.log('click to seatmap, column not found ', col, row);
        }
    }
}
